/**
 * This file defines the styles used in Gutenberg.
 */
#wl-faq-meta-list-box {
  max-height: 380px;
  overflow-y: scroll;
  width: 100%;
  overflow-x: hidden; }

/**
 * WordLift's sidebar.
 *
 * The component is defined in gutenberg/index.js.
 */
.wl-sidebar {
  padding-top: 25px; }

.wl-notice .components-notice {
  margin: 10px 0; }
  .wl-notice .components-notice .components-notice__content {
    margin: 5px 0; }

/**
 * .editor-block-list__block was removed on 5.4.0 and replaced with
 * .block-editor-block-list__block, for WP 5.4.0 compatibility we add this class
 */
.editor-block-list__block .textannotation,
.block-editor-block-list__block .textannotation {
  cursor: pointer;
  transition: all 500ms linear;
  background-color: rgba(46, 146, 255, 0.2);
  padding: 2px; }
  .editor-block-list__block .textannotation.disambiguated, .editor-block-list__block .textannotation.selected,
  .block-editor-block-list__block .textannotation.disambiguated,
  .block-editor-block-list__block .textannotation.selected {
    background-color: transparent;
    box-shadow: none; }
  .editor-block-list__block .textannotation.disambiguated,
  .block-editor-block-list__block .textannotation.disambiguated {
    background-color: rgba(46, 146, 255, 0.4);
    border-color: #007aff !important;
    border-bottom: #999 2px solid; }
  .editor-block-list__block .textannotation.selected,
  .block-editor-block-list__block .textannotation.selected {
    color: #fff;
    background-color: #a4a3a3; }
  .editor-block-list__block .textannotation.disambiguated.selected,
  .block-editor-block-list__block .textannotation.disambiguated.selected {
    color: #fff;
    background-color: #007aff;
    border-bottom: #007aff 2px solid; }

.editor-block-list__block wl-faq-question,
.block-editor-block-list__block wl-faq-question {
  border-bottom: 2px dotted #007aff;
  opacity: 0.9;
  display: inline; }
  .editor-block-list__block wl-faq-question:hover,
  .block-editor-block-list__block wl-faq-question:hover {
    opacity: 1; }

.editor-block-list__block wl-faq-answer,
.block-editor-block-list__block wl-faq-answer {
  border-bottom: 2px dotted #007aff;
  opacity: 0.9;
  display: inline; }
  .editor-block-list__block wl-faq-answer:hover,
  .block-editor-block-list__block wl-faq-answer:hover {
    opacity: 1; }

